
import { Editor, EditorContent } from "@tiptap/vue-3";
import StarterKit from "@tiptap/starter-kit";
import Placeholder from "@tiptap/extension-placeholder";
import Underline from "@tiptap/extension-underline";
import Image from "@tiptap/extension-image";
import { defineComponent } from "vue";

export default defineComponent({
  name: "Editor",
  components: {
    EditorContent
  },
  props: {
    user: Object,
    content: String,
    displayTitle: String,
    displaySubtitle: String
  },
  data() {
    return {
      editor: new Editor({
        extensions: [StarterKit, Underline, Placeholder, Image],
        content: this.content,
        editorProps: {
          attributes: {
            class: "content"
          }
        },
        injectCSS: false
      }),
      title: this.displayTitle,
      subtitle: this.displaySubtitle,
      missingFields: new Set() as Set<String>
    };
  },
  methods: {
    submitPost() {
      const content = this.editor.getHTML()
      this.missingFields = new Set();
      if (this.title && content.length >= 100) {
        this.$emit("post", {
          title: this.title,
          subtitle: this.subtitle,
          content: content
        });
      }
      if (content.length < 100) this.missingFields.add("content");
      if (!this.title) this.missingFields.add("title");
    },
    addImage() {
      const url = window.prompt('URL')

      if (url) {
        this.editor.chain().focus().setImage({ src: url }).run()
      }
    }
  },
  beforeUnmount() {
    // Always destroy your editor instance when it's no longer needed
    this.editor.destroy();
  }
});
